









.mint-toast {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    -webkit-transition: opacity .3s linear;
    transition: opacity .3s linear
}
.mint-toast.is-placebottom {
    bottom: 50px;
    left: 50%;
        -webkit-transform: translate(-50%, 0);
        transform: translate(-50%, 0)
}
.mint-toast.is-placemiddle {
    left: 50%;
    top: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%)
}
.mint-toast.is-placetop {
    top: 50px;
    left: 50%;
        -webkit-transform: translate(-50%, 0);
        transform: translate(-50%, 0)
}
.mint-toast__icon {
    display: block;
    text-align: center;
    font-size: 56px
}
.mint-toast__text {
    font-size: 14px;
    display: block;
    text-align: center
}
.mint-toast__pop-enter, .mint-toast__pop-leave-active {
    opacity: 0
}



