.mark_bg{width:100%;height:100%;position:fixed;top:0;left:0;z-index:2000;background-color:#000;opacity:.5}.dialogBox{width:85%;background:#fff;position:fixed;left:50%;top:50%;z-index:2100;text-align:center;border-radius:6px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);overflow:hidden}.dialogBox .dialog-title{padding-top:1em;font-size:16px;color:#333}.dialogBox .dialog-content{color:#999;font-size:14px;padding:10px 20px 15px;line-height:30px}.dialogBox .btn_box{position:relative}.dialogBox .btn_box:before{content:'';position:absolute;z-index:2;left:0;top:0;width:100%;height:1px;background-image:-webkit-linear-gradient(bottom, #dddee3 50%, transparent 50%);background-image:linear-gradient(0deg, #dddee3 50%, transparent 50%)}.dialogBox .btn_box ul.btn1{overflow:hidden}.dialogBox .btn_box ul.btn1 button{height:44px;line-height:44px;text-align:center;font-size:16px;border:none;display:block;width:100%;color:#59B6EF;background:#fff}.dialogBox .btn_box ul.btn2{overflow:hidden}.dialogBox .btn_box ul.btn2 li{float:left;width:50%}.dialogBox .btn_box ul.btn2 li:first-child{position:relative}.dialogBox .btn_box ul.btn2 li:first-child:before{content:'';position:absolute;z-index:1;right:0;top:0;width:1px;height:100%;background-image:-webkit-linear-gradient(left, #dddee3 50%, transparent 50%);background-image:linear-gradient(90deg, #dddee3 50%, transparent 50%)}.dialogBox .btn_box ul.btn2 button{height:44px;line-height:44px;text-align:center;font-size:16px;border:none;display:block;width:100%;background:#fff}.dialogBox .btn_box ul.btn2 .btn_red button{color:#333}.dialogBox .btn_box ul.btn2 .btn_grey button{color:#59B6EF}.dialogBox .tag_content .input{height:30px;position:relative;margin:0 auto;width:100%}.dialogBox .tag_content .input input{position:absolute;top:1px;left:1px;z-index:200;width:95%;height:28px;font-size:14px;border:none;display:block;line-height:28px;-webkit-appearance:none;background:#fff;padding:0 5px;margin:0}.dialogBox .tag_content .input:after{content:" ";width:200%;height:200%;position:absolute;top:0;left:0;z-index:100;border:1px solid #ACB3BF;-webkit-transform:scale(.5);transform:scale(.5);-webkit-transform-origin:0 0;transform-origin:0 0;box-sizing:border-box;border-radius:4px}


